import React from 'react'
import styled, { ThemeProvider, injectGlobal } from 'styled-components'
import theme from './theme'
import { Button, Label, Head, SubHead, P } from '../components'
import { Grid, Row, Col } from 'react-bootstrap'

injectGlobal`
  @import url('https://maxcdn.bootstrapcdn.com/bootstrap/3.3.7/css/bootstrap.min.css');
  @import url('https://fonts.googleapis.com/css?family=Open+Sans:400,700|Source+Code+Pro:600');
  body {
    margin: 0;
    font-family: 'Open Sans', sans-serif;
    background-color: ${theme.background};
    color: ${theme.main};
    font-size: 13px;
    line-height: 2em;
  }
  h1,h2,h3,h4,h5,h6 {
    line-height: 1.5em;
    margin: 0 0 20px;
  }
  p {
    margin: 0 0 40px;
    max-width: 640px;
  }
  a {
    color: ${theme.main};
    text-decoration: underline;
  }
`
const Container = styled.div`
`
const Header = styled((props) => (<Grid><Row><Col md={12} {...props}/></Row></Grid>))`
  margin: 40px 0;
`
const FooterContainer = styled.div`
  padding: 20px 0;
  background-color: black;
`
const Footer = styled((props) => (<Grid><Row><Col md={12} {...props}/></Row></Grid>))`
`

export default ({ children }) => (
  <ThemeProvider theme={theme}>
    <Container>
      <Header>
        <Head>fata, inc.</Head>
        <P>Hello! we are security company!</P>
      </Header>
      {children()}
      <FooterContainer>
        <Footer>
          <span>fata, inc.&nbsp;</span>
          (contact: <a href='mailto:admin@fata.io'>admin@fata.io</a>)
        </Footer>
      </FooterContainer>
    </Container>
  </ThemeProvider>
)



